import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Row, Col, Button } from 'antd';

class Success extends React.Component {
    constructor(props) {
        super(props);
        ['goToPay'].forEach(m=>this[m] = this[m].bind(this))
    }

    goToPay() {
        hashHistory.push(`/activity/detail/${this.props.params.id}/pay?person=${this.props.location.query.person}`);
    }

    render() {
        const successBG = require("biz-imgs/activity/successBG.png");

        return (
            <Row className="activitySuccess" type="flex" justify="center" align="middle">
                <Col span={12} className="frame">
                    <img src={successBG} />
                    <Row className="prompt">
                        报名成功!{this.props.location.query.type == 1 ? <i>请支付活动费用</i> : <i className="free">本活动为免费活动</i>}
                    </Row>
                    <Row>
                        <Button type="primary" onClick={this.goToPay} className="payBtn" disabled={this.props.location.query.type == 0 ? true : false}>支付活动费用</Button>
                    </Row>
                </Col>
            </Row>
        )
    }
}

module.exports = Success;


